<template>
  <v-row class="fill-height">
    <v-col>
      <v-sheet height="64">
        <v-toolbar flat>
          <v-btn outlined class="mr-4" color="grey darken-2" @click="setToday">
            Today
          </v-btn>
          <v-btn fab text small color="grey darken-2" @click="prev">
            <v-icon small> mdi-chevron-left </v-icon>
          </v-btn>
          <v-btn fab text small color="grey darken-2" @click="next">
            <v-icon small> mdi-chevron-right </v-icon>
          </v-btn>
          <v-toolbar-title v-if="$refs.calendar">
            {{ $refs.calendar.title }}
          </v-toolbar-title>
          <v-spacer></v-spacer>
          <v-menu bottom right>
            <template v-slot:activator="{ on, attrs }">
              <v-btn outlined color="grey darken-2" v-bind="attrs" v-on="on">
                <span>{{ typeToLabel[type] }}</span>
                <v-icon right> mdi-menu-down </v-icon>
              </v-btn>
            </template>
            <v-list>
              <v-list-item @click="type = 'day'">
                <v-list-item-title>Day</v-list-item-title>
              </v-list-item>
              <v-list-item @click="type = 'week'">
                <v-list-item-title>Week</v-list-item-title>
              </v-list-item>
              <v-list-item @click="type = 'month'">
                <v-list-item-title>Month</v-list-item-title>
              </v-list-item>
              <v-list-item @click="type = '4day'">
                <v-list-item-title>4 days</v-list-item-title>
              </v-list-item>
            </v-list>
          </v-menu>
        </v-toolbar>
      </v-sheet>
      <v-sheet height="600">
        <v-calendar
          ref="calendar"
          v-model="focus"
          color="primary"
          :events="events"
          :event-color="getEventColor"
          :type="type"
          @click:event="viewEvent"
          @click:more="viewDay"
          @click:date="viewDay"
        ></v-calendar>
      </v-sheet>
    </v-col>
    <v-dialog
      v-model="dialog"
      hide-overlay
      transition="dialog-bottom-transition"
    >
      <v-card>
        <v-toolbar dark color="primary">
          <v-btn icon dark @click="dialog = false">
            <v-icon>mdi-close</v-icon>
          </v-btn>
          <v-toolbar-title>Booking</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-toolbar-items>
            <v-btn dark text @click="book"> Save </v-btn>
          </v-toolbar-items>
        </v-toolbar>
        <v-container>
          <v-row>
            <v-text-field v-model="name" label="Name" required></v-text-field>
          </v-row>
        </v-container>
      </v-card>
    </v-dialog>
  </v-row>
</template>

<script>
import firebase from "@/plugins/firebaseConfig.js";
const db = firebase.collection("calendar");

export default {
  data: () => ({
    focus: "",
    type: "month",
    typeToLabel: {
      month: "Month",
      week: "Week",
      day: "Day",
      "4day": "4 Days",
    },
    selectedEvent: {},
    events: [],
    colors: [
      "blue",
      "indigo",
      "deep-purple",
      "cyan",
      "green",
      "orange",
      "grey darken-1",
    ],
    dialog: false,
    name: "",
    isEdit: false,
  }),
  mounted() {
    this.$refs.calendar.checkChange();
  },
  async created() {
    const collections = await db.get();
    collections.forEach((doc) => {
      let event = {
        name: doc.data().name,
        color: "blue",
        start: doc.id,
        end: doc.id,
      };
      this.events.push(event);
    });
  },
  methods: {
    viewDay({ date }) {
      this.focus = date;
      this.dialog = true;
    },
    getEventColor(event) {
      return event.color;
    },
    setToday() {
      this.focus = "";
    },
    prev() {
      this.$refs.calendar.prev();
    },
    next() {
      this.$refs.calendar.next();
    },
    async book() {
      //         if(this.isEdit) {
      // const dateRef = db.doc(this.focus);
      // const res = await dateRef.update({name: this.name});
      //         } else {

      //         }
      await db.doc(this.focus).set({ name: this.name });
      let event = {
        name: this.name,
        color: "blue",
        start: this.focus,
        end: this.focus,
      };
      //   monthRef.add(event);
      if (this.isEdit) {
        this.events = this.events.map((item) => {
          if (item.start == this.focus) {
            item.name = this.name;
          }
          return item;
        });
        this.isEdit = false;
      } else {
        this.events.push(event);
      }

      this.dialog = false;
    },

    viewEvent({ event }) {
      console.log(event);
      this.isEdit = true;
      this.focus = event.start;
      this.name = event.name;
      this.dialog = true;
    },
  },
};
</script>